//variables.sass
@import './fonts.css'

:root
    --font-primary: 'Circe Rounded', sans-serif
    --font-secondary: 'Roboto', sans-serif
    --font-size-header: 1.25rem
    --font-size-lg: 1rem
    --font-size-base: 0.875rem
    --font-size-sm: 0.75rem
    --font-weight-bold: 700
    --font-weight-base: 400
    --color-text-primary: #000000
    --color-text-secondary: #293244
    --color-text-muted: #A0A9B8
    --color-text-attention: #E12828
    --color-text-uploader: #3A353E
    --color-text-btn: #fff
    --color-bg: #fff
    --color-accent: #6447CF
    --color-surface-card: #F6F6F8
    --color-surface-load: #DAEDFD
    --color-surface-attention: #FDEEEF
    --color-card-attention: #FADEE0
    --color-progress-completed: #7ABEF8
    --color-progress-nocompleted: #DAEDFD
    --color-btn-default: #45A5F6
    --color-btn-hover: #007EE5
    --color-btn-disabled: #4453711A
    --color-btn-secondary-text: #293244
    --color-btn-secondary-default: #DAEDFD
    --color-btn-secondary-hover: #B5DBFB
    --color-checkbox-checked-default: #45A5F6
    --color-checkbox-checked-hover: #007EE5
    --color-checkbox-nochecked-default: #D0D4DC
    --color-checkbox-nochecked-hover: #45A5F6
    --color-outline-default: #A0A9B8
    --color-outline-focus: #6447CF
    --radius-base: 6px
    --radius-container: 20px
    --radius-full: 100px
    --border-width: 1px
    --validation-spacing: -1rem
    --spacing-xs: 4px
    --spacing-sm: 8px
    --spacing-form: 10px
    --spacing-mds: 12px
    --spacing-upload: 14px
    --spacing-md: 16px
    --spacing-lg: 24px
    --spacing-xl: 32px
    --spacing-big: 48px
    --spacing-ultra: 64px
    --scale-icon: 16px
    --transition-base: 0.3s
